/* background #181944
text #8771CB 
text rectified #D0C7EA*/


.container{
  /* background-color: #1d3334;
  color: #3C9DCD; */
  background-color: #181944;
  color: #D0C7EA;
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: #3C9DCD dashed 1px;
  border-radius: 0.5rem;
  padding: 3rem;
  min-height: 90vh;
}
article{
  font-family: Satisfy;
  font-size: 35px;
  text-align: justify;
}
header{
  border-bottom: #3C9DCD solid 1px;
  border-radius: 0.5rem;
  padding: 1em 1em 0.5em 1em;
  font-size: 40px;
  font-family: Satisfy;
}
article{
  padding: 1rem;
}
footer{
  padding: 1rem;
  text-align: center;
  line-height: 1.5rem;
}
.author{
  text-align:end;
  font-family: Satisfy;
  font-size: 19px;
}

@media only screen and (min-width:1301px) {
  .container{
    width: 60%;
    height: 60%;
  }
}
@media only screen and (max-width:1300px) {
  .container{
    width: 70%;
    height: 70%;
  }
}
@media only screen and (max-width:825px){
  .container{
    width: 85%;
    height: 85%;
  }
}
@media only screen and (max-width:600px){
  .container{
    width: 90%;
    height: 90%;
  }
}