body{
    background-image: linear-gradient(0deg, #182848, #1981b9);
    font-size: 14px;
}

@keyframes AnimateColony1 {
    from{
        left: -10%;
        top: 20%;
    }
    to{
        left: 100%;
        top:35%;
    }
}
@keyframes AnimateColony2{
    from{
        left: -10%;
        top: 80%;
    }
    to{
        left: 100%;
        top:70%;
    }
}
@keyframes AnimateColony3 {
    from{
        left: -10%;
        top: 30%;
    }
    to{
        left: 100%;
        top: 10%;
    }
}
@keyframes AnimateColony4 {
    from{
        left: -10%;
        top: 50%;
    }
    to{
        left: 100%;
        top: 55%;
    }
}
@keyframes AnimateColony5 {
    from{
        right: 100%;
        top: 10%;
    }
    to{
        right: -10%;
        top: 15%;
    }
}
@keyframes AnimateColony6 {
    from{
        right: 100%;
        top: 45%;
    }
    to{
       right: -10%; 
       top: 70%;
    }
}
@keyframes AnimateColony7 {
    from{
        right: 100%;
        top: 75%;
    }
    to{
        right: -10%;
        top: 80%;
    }
}
#colony1{
    position: fixed;
    z-index: -1;
    width: 20%;
    height:fit-content;
    /* opacity: 0.5; */
    animation: AnimateColony1 30s linear infinite;
}
#colony2{
    position: fixed;
    z-index: -1;
    width: 20%;
    height: fit-content;
    /* opacity: 0.5; */
    animation: AnimateColony2 20s linear 4s infinite;
}
#colony3{
    position: fixed;
    z-index: -1;
    width: 20%;
    height: fit-content;
    /* opacity: 0.5; */
    animation: AnimateColony3 25s linear 10s infinite;
}
#colony4{
    position: fixed;
    z-index: -1;
    width: 20%;
    height: fit-content;
    /* opacity: 0.5; */
    animation: AnimateColony4 25s linear 7s infinite;
}
#colony5{
    position: fixed;
    z-index: -1;
    width: 20%;
    height: fit-content;
    /* opacity: 0.5; */
    transform: rotate(0.5turn);
    animation: AnimateColony5 20s linear 2s infinite;
    animation-direction: reverse;
}
#colony6{
    position: fixed;
    z-index: -1;
    width: 20%;
    height: fit-content;
    transform: rotate(183deg);
    /* opacity: 0.5; */
    animation: AnimateColony6 25s linear 6s infinite;
    animation-direction: reverse;
}
#colony7{
    position: fixed;
    z-index: -1;
    width: 20%;
    height: fit-content;
    /* opacity: 0.5; */
    transform: rotate(0.5turn);
    animation: AnimateColony7 20s linear infinite;
    animation-direction: reverse;
}
